/*
* Provides rendering of a table which is using the given FlexibleTableHeader set in
* order to tabulate the given data. As per definition of earch header component,
* a column could be hidden, sortable, or draggable. Each table row can expand/collapse
* or respond to a click action.
*/
import {
    Component,
	Input,
	Output,
	ContentChildren,
	QueryList,
	AfterContentInit,
	ViewContainerRef,
	EventEmitter
} from '@angular/core';

@Component({
	selector: 'flexible-tab',
	template: `    
<ng-container  
    *ngIf="selected && template"
    [ngTemplateOutlet]="template" 
    [ngTemplateOutletContext]="templateContext()"></ng-container>

<ng-content *ngIf="selected && !template"></ng-content>
`,
	styles: [`:host{padding:0;margin:0;width:100%}`]
})
export class FlexibleTabComponent {

    @Input("selected")
    public selected: boolean;

    @Input("title")
    public title: string;

    @Input("tabalticon")
    public tabalticon: string;

    @Input("tabicon")
    public tabicon: string;

    @Input("template")
    public template: any;

    @Input("data")
    public sourceData: any;

    constructor() {}

	templateContext() {
		return {data: this.sourceData };
	}
}


@Component({
	selector: 'flexible-tabs',
	template: `
<div class="flexible-tabs {{type}} {{position}}">
    <div class="tabs-control" role="list" *ngIf="position === 'top' || position === 'left'">
        <a *ngFor="let tab of tabs; let i = index" 
            role="listitem" 
            tabindex="0"
            (keyup)="keyup($event)" 
            (click)="selectTab(i)" 
            [class.selected]="tab.selected">
            <span *ngIf="tab.tabicon && !tab.selected" [class]="tab.tabicon" aria-hidden="true"></span>
            <span *ngIf="tab.tabalticon && tab.selected" [class]="tab.tabalticon" aria-hidden="true"></span>
            <span *ngIf="!tab.tabicon && !tab.tabalticon && isIconified && position === 'left'" class="place-icon" aria-hidden="true"></span>
            <span class="off-screen" [textContent]="tab.message"></span>
            <span [textContent]="tab.title"></span>
        </a>
    </div>
    <div class="tabs-viewport">
        <ng-content></ng-content>
    </div>
    <div class="tabs-control" role="list" *ngIf="position === 'bottom' || position === 'right'">
        <a *ngFor="let tab of tabs; let i = index" 
            role="listitem" 
            tabindex="0"
            (keyup)="keyup($event)" 
            (click)="selectTab(i)" 
            [class.selected]="tab.selected">
            <span *ngIf="tab.tabicon && !tab.selected" [class]="tab.tabicon" aria-hidden="true"></span>
            <span *ngIf="tab.tabalticon && tab.selected" [class]="tab.tabalticon" aria-hidden="true"></span>
            <span *ngIf="!tab.tabicon && !tab.tabalticon && isIconified && position === 'right'" class="place-icon" aria-hidden="true"></span>
            <span class="off-screen" [textContent]="tab.message"></span>
            <span [textContent]="tab.title"></span>
        </a>
    </div>
</div>
`,
	styles: [`.flexible-tabs{display:-webkit-box;display:-ms-flexbox;display:flex;width:100%}.flexible-tabs .place-icon{width:17px;height:16px;display:inline-block}.flexible-tabs .off-screen{display:block;float:left;height:0;overflow:hidden;text-indent:-99999px;width:0}.flexible-tabs .tabs-viewport{-webkit-box-sizing:border-box;box-sizing:border-box}.flexible-tabs .tabs-control{z-index:2}.flexible-tabs .tabs-control a{-webkit-box-sizing:border-box;box-sizing:border-box;padding:2px 15px}.flexible-tabs .tabs-control a.selected{font-weight:700}.flexible-tabs.top{-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column}.flexible-tabs.top.plain .tabs-control a{margin:0 2px}.flexible-tabs.top.plain .tabs-viewport{margin:5px 0}.flexible-tabs.top.button .tabs-control a{border:1px solid #888;margin:0 2px;border-radius:4px;background-color:#ccc}.flexible-tabs.top.button .tabs-control a.selected{background-color:#2e32ff;color:#fff}.flexible-tabs.top.button .tabs-viewport{margin:5px 0}.flexible-tabs.top.image .tabs-control a{margin:0 2px}.flexible-tabs.top.image .tabs-viewport{border:1px solid #ddd}.flexible-tabs.top.tab .tabs-control{margin-bottom:-1px}.flexible-tabs.top.tab .tabs-control a{border:1px solid #ddd;background-color:#fff;border-radius:4px 4px 0 0}.flexible-tabs.top.tab .tabs-control a.selected{border-bottom-color:#fff}.flexible-tabs.top.tab .tabs-viewport{border:1px solid #ddd;padding:10px}.flexible-tabs.bottom{-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column}.flexible-tabs.bottom.plain .tabs-control a{margin:0 2px}.flexible-tabs.bottom.plain .tabs-viewport{margin:5px 0}.flexible-tabs.bottom.button .tabs-control a{border:1px solid #888;margin:0 2px;border-radius:4px;background-color:#ccc}.flexible-tabs.bottom.button .tabs-control a.selected{background-color:#2e32ff;color:#fff}.flexible-tabs.bottom.button .tabs-viewport{margin:5px 0}.flexible-tabs.bottom.image .tabs-viewport{border:1px solid #ddd}.flexible-tabs.bottom.image .tabs-control a{margin:0 2px}.flexible-tabs.bottom.tab .tabs-control{margin-top:-1px}.flexible-tabs.bottom.tab .tabs-control a{border:1px solid #ddd;background-color:#fff;border-radius:0 0 4px 4px}.flexible-tabs.bottom.tab .tabs-control a.selected{border-top-color:#fff}.flexible-tabs.bottom.tab .tabs-viewport{border:1px solid #ddd;padding:10px}.flexible-tabs.left{-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row}.flexible-tabs.left.plain .tabs-control{-webkit-box-flex:0;-ms-flex:0 0 10%;flex:0 0 10%;border-right:1px solid #ddd}.flexible-tabs.left.plain .tabs-control a{margin:0 2px;width:100%;display:table}.flexible-tabs.left.plain .tabs-viewport{-webkit-box-flex:1;-ms-flex:1;flex:1;margin:5px 0;padding-left:10px}.flexible-tabs.left.button .tabs-control{-webkit-box-flex:0;-ms-flex:0 0 10%;flex:0 0 10%}.flexible-tabs.left.button .tabs-control a{border:1px solid #888;margin:0 2px;border-radius:4px;background-color:#ccc;width:100%;display:table}.flexible-tabs.left.button .tabs-control a.selected{background-color:#2e32ff;color:#fff}.flexible-tabs.left.button .tabs-viewport{-webkit-box-flex:1;-ms-flex:1;flex:1;margin:5px 0;padding:10px}.flexible-tabs.left.image .tabs-control{-webkit-box-flex:0;-ms-flex:0 0 10%;flex:0 0 10%}.flexible-tabs.left.image .tabs-control a{width:100%;display:table;margin:0 2px}.flexible-tabs.left.image .tabs-viewport{-webkit-box-flex:1;-ms-flex:1;flex:1;border:1px solid #ddd}.flexible-tabs.left.tab .tabs-control{-webkit-box-flex:0;-ms-flex:0 0 10%;flex:0 0 10%;margin-right:-1px}.flexible-tabs.left.tab .tabs-control a{border:1px solid #ddd;background-color:#fff;float:left;border-radius:4px 0 0 4px;width:100%;display:table}.flexible-tabs.left.tab .tabs-control a.selected{border-right-color:#fff}.flexible-tabs.left.tab .tabs-viewport{-webkit-box-flex:1;-ms-flex:1;flex:1;border:1px solid #ddd;padding:10px}.flexible-tabs.right{-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-direction:row;flex-direction:row}.flexible-tabs.right.plain .tabs-control{-webkit-box-flex:0;-ms-flex:0 0 10%;flex:0 0 10%;border-left:1px solid #ddd}.flexible-tabs.right.plain .tabs-control a{margin:0 2px;width:100%;display:table}.flexible-tabs.right.plain .tabs-viewport{-webkit-box-flex:1;-ms-flex:1;flex:1;margin:5px 0}.flexible-tabs.right.button .tabs-control{-webkit-box-flex:0;-ms-flex:0 0 10%;flex:0 0 10%}.flexible-tabs.right.button .tabs-control a{border:1px solid #888;margin:0 2px;border-radius:4px;background-color:#ccc;width:100%;display:table}.flexible-tabs.right.button .tabs-control a.selected{background-color:#2e32ff;color:#fff}.flexible-tabs.right.button .tabs-viewport{-webkit-box-flex:1;-ms-flex:1;flex:1;margin:5px 0;padding:10px}.flexible-tabs.right.image .tabs-control{-webkit-box-flex:0;-ms-flex:0 0 10%;flex:0 0 10%}.flexible-tabs.right.image .tabs-control a{margin:0 2px;width:100%;display:table}.flexible-tabs.right.image .tabs-viewport{-webkit-box-flex:1;-ms-flex:1;flex:1;border:1px solid #ddd}.flexible-tabs.right.tab .tabs-control{-webkit-box-flex:0;-ms-flex:0 0 10%;flex:0 0 10%;margin-left:-1px}.flexible-tabs.right.tab .tabs-control a{border:1px solid #ddd;background-color:#fff;display:table;float:left;border-radius:0 4px 4px 0;width:100%}.flexible-tabs.right.tab .tabs-control a.selected{border-left-color:#fff}.flexible-tabs.right.tab .tabs-viewport{-webkit-box-flex:1;-ms-flex:1;flex:1;border:1px solid #ddd;padding:10px}`]
})
export class FlexibleTabsComponent implements AfterContentInit  {
	tabs = [];
	selectedIndex = -1;
	isIconified = false;

	@ContentChildren(FlexibleTabComponent) children: QueryList<FlexibleTabComponent>;

    @Input("position")
    public position: string; // top, left, bottom, right

    @Input("type")
    public type: string; // button, tab, plain, image

	@Output('onchange')
	private onchange = new EventEmitter();

    constructor() {}

	ngAfterContentInit() {
		this.children.forEach((tabInstance, index) => {
			if(tabInstance.selected) {
				this.selectedIndex = index;
			}
				this.isIconified = true;
			if (tabInstance.tabicon || tabInstance.tabalticon) {
				this.isIconified = true;
			}
			this.tabs.push(tabInstance);
		});
	}

	keyup(event) {
        const code = event.which;
		
		if (code === 13) {
			event.target.click();
		}
	}
	selectTab(index) {
		this.tabs.map((tab)=>{
			tab.selected = false;
		});
		this.tabs[index].selected = true;
		this.selectedIndex = index;
		this.onchange.emit({
			selectedIndex: index,
			selectedTitle: this.tabs[index].title
		});
	}
}
